import { Component } from '@angular/core';
import { OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Section, Publication, ALL } from './publications';
import { PublicationsService } from './publications.service';

@Component({
  templateUrl: './publications.component.html',
  providers: [PublicationsService]
})
export class PublicationsComponent implements OnInit {
  author: string = ALL;
  sections: Section[];

  constructor(private publicationsService: PublicationsService,
              private route: ActivatedRoute) {}

  onAnchorClick(): void {
    this.route.fragment.subscribe(f => {
      const element = document.querySelector("#" + f);
      if (element) {
        element.scrollIntoView(element);
        let top = (element as any).offsetTop;
        window.scrollTo(0, top);
      }
    });
  }

  getSections(): void {
    this.publicationsService.getSections().then(sections => this.sections = sections);
  }

  sectionHref(section: Section): string {
    return section.name.replace(/([\W-])/g, '').toLowerCase();
  }

  publicationHtml(pub: Publication): string {
    let joined_refs = '';
    for (let ref of pub.refs) {
      joined_refs += '[<a class="link-sytle" href="' + ref.href + '">' + ref.text + '</a>] ';
    }
    return joined_refs;
  }

  ngOnInit(): void {
    this.getSections();
  }
}



