import { Injectable } from '@angular/core';

import { Section } from './publications';
import { SECTIONS } from './publications.data';

@Injectable()
export class PublicationsService {
  getSections(): Promise<Section[]> {
    return Promise.resolve(SECTIONS);
  }
}



